import { SingleUploadService } from './../../../../../components/document/single-upload.service';
import { Acara } from 'src/app/sdk/hr/models/acara';
import { CompanyAcaraService, CompanyDepartemenService, CompanyKantorService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { Departemen, Kantor, Divisi } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyAcaraService: CompanyAcaraService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    data: Acara = {};
    listDepartemen: Departemen[] = [];
    listKantor: Kantor[] = [];
    listDivisi: Divisi[] = [];
    toogleUpload = false
    ngOnInit() {
        this.id_acara = this.activatedRoute.snapshot.paramMap.get('id_acara');
        this.form();
        this.getData()
        this.getDepartement();
        this.getPreeData();

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company_event.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    getPreeData() {
        this.listKantor = []
        this.companyKantorService.getCompanyKantors().subscribe(
            data => {
                this.listKantor = data.data;
            }
        )
    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.id_departemen,
                status: 'Active'
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    getDepartement() {
        this.companyDepartemenService.getCompanyDepartemens().subscribe(
            data => {
                this.listDepartemen = data.data;
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    id_acara: string = "";
    getData() {
        this.spinner.show()
        this.companyAcaraService.getCompanyAcarasId(this.id_acara).subscribe(
            data => {
                let xx= data.data;
                xx.id_kantor = JSON.parse(xx.id_kantor)
                this.dataForm.setValue(xx)
                this.spinner.hide()


                this.singleUploadService.param.next({
                    for_module: "company_event",
                    for_table: "erp.acara",
                    for_module_id: data.data.id_acara
                })
            }
        )
    }


    dataForm: FormGroupTypeSafe<Acara>;
    form() {
        this.dataForm = this.fb.group<Acara>(
            {
                id_acara: new FormControl('', [Validators.required]),
                judul_acara: new FormControl('', [Validators.required]),
                tanggal_mulai: new FormControl(''),
                tanggal_selesai: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                peruntukan: new FormControl(''),
                id_data_flow: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                status: new FormControl(''),
                posisi: new FormControl(''),
                nomor: new FormControl(''),
                id_departemen: new FormControl(''),
                id_kantor: new FormControl(''),
                id_divisi: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_user_create: new FormControl('')
            }
        )
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyAcaraService.putCompanyAcarasId({
            id: this.id_acara,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/event/list'])
    }

}
