import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { Acara } from 'src/app/sdk/hr/models';
import { CompanyAcaraService } from 'src/app/sdk/hr/services';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private companyAcaraService: CompanyAcaraService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }



    dataAcara: Acara = {}


    id_acara: string = ''

    ngOnInit() {
        this.id_acara = this.routerActive.snapshot.paramMap.get('id_acara')
        console.log(this.id_acara)
        this.getAcara();
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getAcara();
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getAcara() {
        this.spinner.show()
        this.companyAcaraService.getCompanyAcarasId(this.id_acara).subscribe(
            response => {
                this.dataAcara = response.data

                this.params = {
                    id: response.data.id_acara,
                    id_data_flow_detail: response.data.id_data_flow_detail,
                    id_data_flow: response.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_acara"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )

    }


    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_acara })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {

        this.router.navigate(['/hr/company/module/event/list'])
    }

}
